.header {
    position: fixed;
    top: 0;
    left: 0;
    padding-left: 30px;
    width: 100%;
    height: 65px;
    display: flex;
    align-items: center;
    flex-direction: row;
    background-color: transparent;
    z-index: 500;

    .nav_link {
        color: black;
        display: flex;
        align-items: center;
        justify-content: center;
        text-decoration: none;
        font-weight: 600;
        margin-left: 40px;
        padding: 7px 0px 5px 0px;
    }
}
.white {
    background-color: white;
}