.videoList__container {
    position: relative;
    top: 65px;
    .videoListName {
        padding-left: 30px;
        padding-top: 30px;
        display: inline;
    }
    .deleteBtn {
        width: 95px;
        color: #011993;
        font-size: 17px;
        line-height: 30px;
        background-color: transparent;
        border-width: 0px;
        border-style: none;
        border-color: transparent;
        border-radius: 40px;
        outline: none 0px;
        cursor: pointer;
        transition: background-color 0.1s linear opacity 0.1s linear;
        box-shadow: 0px 1px 5px 2px rgba(0, 0, 0, 0.3);
        margin: 13px 0px 0px 16px;
    }
    .Listsection__container {
        margin: 25px 15px 25px 30px;
        display: grid;
        grid-template-columns: repeat(auto-fill, 320px);
        grid-gap: 25px;
    }
    .noVideoMessage {
        padding: 30px;
    }
}